.switch{position:relative;display:inline-block;width:92px;height:27px;margin-bottom:0;cursor:pointer}.switch input{opacity:0;width:0;height:0}.switch input:checked+.slider:before{-webkit-transform:translateX(26px);-ms-transform:translateX(26px);transform:translateX(46px)}.switch input:focus+.slider{box-shadow:0 0 2px #ccc}.switch input:checked ~ span.yes{color:white}.switch input:checked ~ span.no{color:#afafaf}.switch .slider{overflow:hidden;position:absolute;top:0;left:0;right:0;bottom:0;background:linear-gradient(90deg, #2362C7 50%, #d95868 50%);border-radius:34px;-webkit-transition:.4s;transition:.4s}.switch .slider:before{position:absolute;content:"";height:27px;width:46px;left:0;bottom:0;background-color:#fff;-webkit-transition:.4s;transition:.4s}.switch .yes{color:#afafaf;font-size:15px;font-weight:500;position:absolute;top:2.5px;left:10px;transition:color .4s}.switch .yes:before{content:'Yes'}.switch .no{color:#fff;font-size:15px;font-weight:500;position:absolute;top:2.5px;right:10px;transition:color .4s}.switch .no:before{content:'No'}
